body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  flex-shrink: 0;
  height: 40px;
}

.nav__list {
  align-items: center;
  margin: 0;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  position: relative;
}

.nav__link {
  color: inherit;
  flex-shrink: 0;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

ul {
  list-style-type: none;
}

